import { Component, OnInit } from '@angular/core';
import { EmpleadoServiceService } from 'src/app/services/empleado-service.service';


@Component({
  selector: 'app-criterio',
  templateUrl: './criterio.component.html',
  styleUrls: ['./criterio.component.css']
})
export class CriterioComponent implements OnInit {

  b:any[]= [];

  istA:number =0;
  istDif:number =0;

  tmoA:number =0;
  tmoDif:number = 0;

  codA:number =0;
  codDif:number=0;

  reiA:number =0;
  reiDif:number =0;

  llamA:number =0;
  llamDif:number =0;

  //graficade barras
  public barChartLabels:string[] = ['IST', 'TMO', 'Codificación', 'Reiteración', 'Llamadas Finalizadas', ''];
  public barChartType:string = 'bar';
  public barChartLegend:boolean = true;
  public barChartData:any[];

  isDataAvailable: boolean = false;

  constructor(private afs: EmpleadoServiceService) { }

  ngOnInit() {
    this.afs.getData().subscribe(result =>{
      this.b = result.map(a => a.data);
      
      for(let cri of this.b){
        if(cri.ist == '50.0'){
          this.istA +=1;
        }
        else{
          this.istDif +=1;
        }
      }

      for(let cri of this.b){
        if(cri.tmo == '5.0'){
          this.tmoA +=1;
        }
        else{
          this.tmoDif +=1;
        }
      }
      for(let cri of this.b){
        if(cri.codificacion == '5.0'){
          this.codA +=1;
        }
        else{
          this.codDif +=1;
        }
      }
      for(let cri of this.b){
        if(cri.reiteracion == '35.0'){
          this.reiA +=1;
        }
        else{
          this.reiDif +=1;
        }
      }
      for(let cri of this.b){
        if(cri.llamadasFinalizadas == '5.0'){
          this.llamA +=1;
        }
        else{
          this.llamDif +=1;
        }
      }


      this.barChartData = [
        {data: [this.istA, this.tmoA, this.codA, this.reiA, this.llamA, 0], label: 'Mayor'},
        {data: [this.istDif, this.tmoDif, this.codDif, this.reiDif, this.llamDif, 0], label: 'Menor'}
      ];
      this.isDataAvailable = true;
    });
  }

  public chartClicked(e:any):void {
    console.log(e);
  }
 
  public chartHovered(e:any):void {
    console.log(e);
  }
}
