import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { EmpleadoServiceService } from '../../services/empleado-service.service';
import { Empleado } from '../../trabajador/empleado.model';

@Component({
  selector: 'app-todos',
  templateUrl: './todos.component.html',
  styleUrls: ['./todos.component.css']
})
export class TodosComponent implements OnInit {
trabajadores:any;

EmpleadoLista:Empleado[];

  constructor(private empleadoService : EmpleadoServiceService) {}
  
  listData: MatTableDataSource<any>;
displayedColumns:string[] = ['id','nombre','tmo','ist','codificacion','reiteracion','llamadasFinalizadas','ponderacion','opciones'];
@ViewChild(MatPaginator) paginator: MatPaginator;  
@ViewChild(MatSort) sort: MatSort;

  ngOnInit() {

    this.empleadoService.getDataMaterial().subscribe(
      lista =>{
        let array = lista.map(item =>{
          return{
            $key:item.key,
            ...item.payload.val()
          };
        });
        this.listData = new MatTableDataSource(array);
        this.listData.sort = this.sort;
        this.listData.paginator= this.paginator
      });

  }

  applyFilter(filterValue: string) {
    this.listData.filter = filterValue.trim().toLowerCase();
  }

  onEdit(empleado : Empleado){
    this.empleadoService.selecionEmpleado = Object.assign({},empleado);
  }

  onDelete(key:string){
    if(confirm("Estas Seguro que quieres Borrar el registro?") == true){
      this.empleadoService.deleteEmpleado(key);
      /* this.tostr.warning("Eliminacion Exitosa", "Empleado registrado"); */
    }
  }

}
