import { Component, OnInit } from '@angular/core';
import {NgForm} from '@angular/forms'
import { EmpleadoServiceService } from '../../services/empleado-service.service';
import { MatTableDataSource } from '@angular/material';


@Component({
  selector: 'app-nuevo',
  templateUrl: './nuevo.component.html',
  styleUrls: ['./nuevo.component.css']
})
export class NuevoComponent implements OnInit {
  

  constructor(private empleadoService : EmpleadoServiceService) { }

  
  ngOnInit() {
    this.resetForm();
   }
 
   onSubmit(empleadoForm : NgForm){
     if(empleadoForm.value.$key == null)
     {
       this.empleadoService.insertEmpleado(empleadoForm.value);
       this.resetForm(empleadoForm);
     }else{
       this.empleadoService.updateEmpleado(empleadoForm.value);
       this.resetForm(empleadoForm);
     }
   }
 
   resetForm(empleadoForm? : NgForm){
     if(empleadoForm != null){
       empleadoForm.reset();
       this.empleadoService.selecionEmpleado = {
         $key: null,
         nombre: 'Ejemplo',
         id: 'Ejemplo',
         tmo: '5.0',
         ist: '50.0',
         reiteracion:'35.0',
         codificacion:'5.0',
         llamadasFinalizadas:'5.0',
         ponderacion: 'A'
       }
     }
   }

}
