import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';
import { Empleado } from '../trabajador/empleado.model';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class EmpleadoServiceService {

listaempleado:AngularFireList<any>;
selecionEmpleado: Empleado = new Empleado();

  constructor(private fb:AngularFireDatabase) { }

  
  getData(){
    return this.fb.list('TKMhp')
    .snapshotChanges().pipe(map(items => {            // <== new way of chaining
       return items.map(a => {
        const data = a.payload.val();
        const key = a.payload.key;
        return {data};           // or {key, ...data} in case data is Obj
      });
    }));
  }

  getponderacion(){
    return this.fb.list('TKMhp', ref => ref.orderByChild('ponderacion'))
    .snapshotChanges().pipe(map(items => {            // <== new way of chaining
      return items.map(a => {
       const data = a.payload.val();
       const key = a.payload.key;
       return {data};           // or {key, ...data} in case data is Obj
     });
   }));
  }

  getDataMaterial(){
    this.listaempleado = this.fb.list('TKMhp')
    return this.listaempleado.snapshotChanges();
  }

  
  insertEmpleado(empleado : Empleado){
    this.listaempleado.push({
      nombre: empleado.nombre,
      id: empleado.id,
      ponderacion: empleado.ponderacion,
      tmo: empleado.tmo,
      ist: empleado.ist,
      codificacion: empleado.codificacion,
      reiteracion: empleado.reiteracion,
      llamadasFinalizadas: empleado.llamadasFinalizadas
    });
    }
  
     
    updateEmpleado(empleado : Empleado){
      this.listaempleado.update(empleado.$key,{
        nombre: empleado.nombre,
        id: empleado.id,
        ponderacion: empleado.ponderacion,
        tmo: empleado.tmo,
        ist: empleado.ist,
        codificacion: empleado.codificacion,
        reiteracion: empleado.reiteracion,
        llamadasFinalizadas: empleado.llamadasFinalizadas
      });
    }
  
    deleteEmpleado($key : string){
      this.listaempleado.remove($key);
    }
}
