import { Component, OnInit } from '@angular/core';
import { EmpleadoServiceService } from '../../services/empleado-service.service';

@Component({
  selector: 'app-ponderacion',
  templateUrl: './ponderacion.component.html',
  styleUrls: ['./ponderacion.component.css']
})
export class PonderacionComponent implements OnInit {

  b:any[]= [];

  ponA: number = 0;
  ponAper: number;
  ponBper: number;
  ponCper: number;
  ponDper: number;
  ponB:number = 0;
  ponC:number = 0;
  ponD:number = 0;

  //Variables de la grafica de donas
  ChartLabels: string[] = ['Ponderación A', 'Ponderación B', 'Ponderación C', 'Ponderación D'];
  public ChartData:number[] = [];
  public ChartType:string = 'pie';
  public ChartColors: any[] = [{ backgroundColor: ["#45CB24", "#E0E31F", "#FF820D", "#E51C08"] }];
  isDataAvailable: boolean = false;

  constructor(private afs: EmpleadoServiceService) {

   }

  ngOnInit() {
    this.afs.getponderacion().subscribe(result =>{
      this.b = result.map(a => a.data);
      //console.log(this.b);
      for(let pon of this.b){
        if(pon.ponderacion == 'A')
        {
          this.ponA += 1;
          this.ponAper = (this.ponA / this.b.length) * 100;
        }
    
        if(pon.ponderacion == 'B'){
          this.ponB +=1;
          this.ponBper = (this.ponB / this.b.length) * 100;
        }
    
        if(pon.ponderacion == 'C'){
          this.ponC +=1;
          this.ponCper = (this.ponC / this.b.length) * 100;
        }

        if(pon.ponderacion == 'D'){
          this.ponD +=1;
          this.ponDper = (this.ponD / this.b.length) * 100;
        }
       } 
      this.ChartData = [this.ponA, this.ponB, this.ponC, this.ponD];

      this.lineChartData = [
        {data:[3, 6, this.ponA], label:'Ponderacion A'},
        {data:[4, 2, this.ponB], label:'Ponderacion B'},
        {data:[2, 3, this.ponC], label:'Ponderacion C'},
        {data:[1, 3, this.ponD], label:'Ponderacion D'}
      ];
      this.isDataAvailable = true;
       });
  }
 
 
  
  //lineas
  public lineChartColors:Array<any> = [
    { // grey
      backgroundColor: 'rgba(148,159,177,0.2)',
      borderColor: 'rgba(148,159,177,1)',
      pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(148,159,177,0.8)'
    },
    { // dark grey
      backgroundColor: 'rgba(77,83,96,0.2)',
      borderColor: 'rgba(77,83,96,1)',
      pointBackgroundColor: 'rgba(77,83,96,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(77,83,96,1)'
    },
    { // grey
      backgroundColor: 'rgba(148,159,177,0.2)',
      borderColor: 'rgba(148,159,177,1)',
      pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(148,159,177,0.8)'
    }
  ];

    public lineChartData:Array<any>  = [];
    public lineChartLabels:Array<any> = ['Agosto', 'Septiembre', 'Octubre'];
    public lineChartOptions:any = {
      responsive: true
    };
    public lineChartLegend:boolean = true;
    public lineChartType:string = 'line';




 // events
  public chartClicked(e:any):void {
    console.log(e);
  }
 
  public chartHovered(e:any):void {
    console.log(e);
  } 

}
